import React, { useState } from "react";
import Insidenav from "../insidenav";

function Add() {
  const [name1, setName1] = useState("");
 

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
  }
  

  function handleClick(event) {
    var n = name1.length;
    setHeading(n);

    event.preventDefault();
  }

  return (
    <div>
        <Insidenav />

    <div className="container-fluid calci">

      <h1 className="calci">Total Number of Characters = {headingText}</h1>
            <p4>Paste Your Text Below</p4>

      <form onSubmit={handleClick}>
          
        <textarea
          cols="90"
          className="calciip-charcount"
          onChange={handleChange1}
          type="text"
          placeholder="NOTE : Pull the bottom right corner of this box to expand the area of this box for better reading ! "
          value={name1}
          required
        />
         
      
        
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About Total Number Of Characters</h4>
      <p3>This calculator is made for anyone who wants to know the length of a text or essay or passage or pattern or anything of that sort. Below are the most familiar use cases !</p3>
      <br /><br /><h4>Why This ?</h4>
      <p3>COUNT THE NO. OF DOTS : "<b>........................................</b>" <br /> So do you want to strain your eyes for something that is actually going to take less than a second for a computer to find it ?. Time is precious ! Eyes TOO !! LOL</p3>
      

    </div>
    </div>
  );
}

export default Add;
