import React, { useState } from "react";
import Insidenav from "../insidenav";

function SR() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }

  function handleClick(event) {
    var result = (name1 / name2)*100;
    var finalresult = result.toFixed(2);
    setHeading(finalresult);

    event.preventDefault();
  }

  return (
  <div>
       <Insidenav />

    <div className="container-fluid calci">
      <h1>Strike Rate of Batsman = {headingText}</h1>
      <form onSubmit={handleClick}>
        <input
          className="calciip"
          onChange={handleChange1}
          type="number"
          placeholder="Enter Runs Scored"
          value={name1}
        />
        <input
           className="calciip"
          onChange={handleChange2}
          type="number"
          placeholder="No. of Balls"
          value={name2}
        />
        <button className="calcibutton" type="submit">Find</button>
      </form>

    </div>
    <div className="container mt-3 mb-3">
      <h4>About Strike-Rate</h4>
      <p3>Batting strike rate is a measure of how quickly a batsman achieves the primary goal of batting, namely scoring runs.</p3>
      <br /><br /><h4>Example</h4>
      <p3>If a batsman scores 76 runs in 76 balls, then the strike rate is calculated by dividing the no. of runs and the no. of balls which is finally multiplied with 100. In this case it is (76/76)*100 which is 100.00</p3>
      <br /><br /><h4>You Know What ?</h4>
      <p3>JEC Franklin of New Zealand has the highest ever Strike rate in a single innings which is 387.50. He achieved this feet on March 13 2011 during New Zealand vs Canada match by scoring 31 runs of just 8 balls.</p3>

    </div>
  </div>
  );
}

export default SR;
