import React from "react";
import Avatar from "./avatar";
import Detail from "./detail";
import { Link } from "react-router-dom";
function Card(props) {
  return (
    
     <div class="col">
    <div class="card h-100">
      <Avatar img={props.img} />
      <div class="card-body">
        <h5 class="card-title">{props.name}</h5>
        <p1 class="card-text">{props.email}</p1>
        <Link to={props.link} className="align-self-end btn btn-info btn-sm btn-block">Calculate</Link>
      </div>
    </div>
  </div>

  );
}
export default Card;
