import React, { useState } from "react";
import Insidenav from "../insidenav";
function Add() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }

  function handleClick(event) {
    var result = name1 / name2;
    var finalresult = result.toFixed(2);
    setHeading(finalresult);

    event.preventDefault();
  }

  return (
    <div>
          <Insidenav />

    <div className="container-fluid calci">
      <h1 className="calci">Bowler Economy = {headingText}</h1>
      <form onSubmit={handleClick}>
        <input
          className="calciip"
          onChange={handleChange1}
          type="number"
          placeholder="Enter Runs conceded"
          value={name1}
        />
        <input
          className="calciip"
          onChange={handleChange2}
          type="number"
          placeholder="Enter no. of overs bowled"
          value={name2}
        />
        <button className="calcibutton" type="submit">Find</button>
      </form>
      </div>
      <div className="container mt-3 mb-3">
      <h4>About Bowler-Economy</h4>
      <p3>In cricket, a bowler's economy rate is the number of runs they have conceded per over bowled. It is one of a number of statistics used to compare bowlers, commonly used alongside bowling average and strike rate to judge the overall performance of a bowler.</p3>
      <br /><br /><h4>Example</h4>
      <p3>If a bowler bowls for 4 overs and concedes 34 runs, then the economy is calculated by dividing the no. of runs conceded and the no. of overs bowled. In this case it is 34/4 which is 8.5</p3>
      <br /><br /><h4>You Know What ?</h4>
      <p3>PV Simmons of West Indies has the best bowler economy in a single innings. He achieved this feet on 17 Dec 1992 during West Indies Vs Pakistan Match where he bowled for 10 overs and with 8 maiden overs and conceded just 3 runs and took 4 wickets ! His economy for that match was 0.30 ! In that match West Indies scored 214 runs for 9 wickets and Pakistan, who were chasing were bowled out for 81 runs.</p3>

    </div>
    </div>
  );
}

export default Add;
