import React, { useState } from "react";
import Insidenav from "../insidenav";


function Add() {
  
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [headingText, setHeading] = useState("");


 

  
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handlereset() {
    setName3("");
    setName4("");
    setName5("");
    setHeading("");
    
  }
  
 
  function handleClick(event) {
    
   var roi = (name4 - name3)/name3;
   var years = 1/name5;
   var ans = Math.pow(roi+1,years);
   setHeading((ans-1)*100);

   

   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />

    <div className="container-fluid calci">

      <h1 className="calci">Return of Investment = {headingText} %</h1>

      <form onSubmit={handleClick} autoComplete="off" required>
      <label for="bin">Investment Amount (  )</label>
      <input className="calciipsmall" placeholder="" type="text" id="bin" onChange={handleChange3} value={name3} required />
      <label for="oct">Returned Amount (  )</label>
      <input className="calciipsmall" placeholder="" type="text" id="oct" onChange={handleChange4} value={name4} required />
      <label for="dec">Investment length ( years )</label>
      <input className="calciipsmall" placeholder="" type="text" id="dec" onChange={handleChange5} value={name5} required />
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About the number systems</h4>
      <p3>Binary numbers are important because using them instead of the decimal system simplifies the design of computers and related technologies.<br /><br />The main advantage of using Octal numbers is that it uses less digits than decimal and Hexadecimal number system. So, it has fewer computations and less computational errors. It uses only 3 bits to represent any digit in binary and easy to convert from octal to binary and vice-versa.<br /><br />Hexadecimal can be used to write large binary numbers in just a few digits. It makes life easier as it allows grouping of binary numbers which makes it easier to read, write and understand. It is more human-friendly, as humans are used to grouping together numbers and things for easier understanding. </p3>
      
    </div>
    </div>
  );
}

export default Add;
