import React, { useState } from "react";
import Insidenav from "../insidenav";

function Add() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [name6, setName6] = useState("");



  const [headingText1, setHeading1] = useState("");
  const [headingText2, setHeading2] = useState("");


  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
  }function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  }function handleChange6(event) {
    console.log(event.target.value);
    setName6(event.target.value);
  }
  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setName4("");
    setName5("");
    setName6("");

    setHeading1("");
    setHeading2("");

  }
  function handleClick(event) {
    var deno = (name1 * name5) - (name2 * name4);
    setHeading1(((name3 * name5)-(name2 * name6))/deno);
    setHeading2(((name1 * name6)-(name3 * name4))/deno);


    event.preventDefault();
  }

  return (
    <div>
            <Insidenav />

    <div className="container-fluid calci">
  <h1 className="calci">X = {headingText1} <br />Y = {headingText2}</h1>
      <form onSubmit={handleClick}>
      <div>
      
        <div className="inner">
        <label className="para1" for="x1"> X&nbsp;&nbsp;&nbsp; +&nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange1}
          type="number"
          placeholder=""
          value={name1}
          name="x1"
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">Y&nbsp;&nbsp;&nbsp; = </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange2}
          type="number"
          placeholder=""
          value={name2}
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1"> ----- (1)</label>
        <input
          className="calciipunknown para2"
          onChange={handleChange3}
          type="number"
          placeholder=""
          value={name3}
          required
        />
        </div>
      </div>
      <div>
        <div className="inner">
        <label className="para1" for="x1"> X&nbsp;&nbsp;&nbsp; +&nbsp;&nbsp;&nbsp; </label>

        <input
          className="calciipunknown para2"
          onChange={handleChange4}
          type="number"
          placeholder=""
          value={name4}
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1"> Y&nbsp;&nbsp;&nbsp; =  </label>

        <input
          className="calciipunknown para2"
          onChange={handleChange5}
          type="number"
          placeholder=""
          value={name5}
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">----- (2)</label>

        <input
          className="calciipunknown para2"
          onChange={handleChange6}
          type="number"
          placeholder=""
          value={name6}
        />
        </div>
      </div>    
        <button className="calcibutton" type="submit">Find X and Y</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      </div>
      <div className="container mt-3 mb-3">
      <h4>How To USE it ?</h4>
      <p3>The Caluclator is very simple. If Your equation is <br /><br /> 2x+3y=12 <br />3x-4y=1 <br /><br /> Now to find x and y, you need to enter 2 in the first box, 3 in the second and 12 in the third. For the second equation you have to enter 3 in the first box, -4 in the second since a + sign is preceded and 1 in the third boxes. Plese be careful with the signs of the input to get proper results!</p3>
      

    </div>
    </div>
  );
}

export default Add;
