import React, { useState } from "react";
import Insidenav from "../insidenav";

function Add() {
  const [name1, setName1] = useState("");

  const [headingText, setHeading] = useState("");
  const [number, setNumber] = useState("");


  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
    setNumber("");
    
  }

  function handleClick(event) {
    var result = name1.split(' ');
    var sum=0;
    var ans= 0;
    for(var i=0; i<result.length; i++){
      sum += 1/parseFloat(result[i]);
      ans = (1/sum).toFixed(4);
    }
    setNumber(i);
    setHeading(ans+" ");

    event.preventDefault();
  }

  return (
    <div>
            <Insidenav />

    <div className="container-fluid calci">

      <h1 className="calci">Sum of {number} Resistances in parallel = {headingText}</h1>
      <form onSubmit={handleClick} autoComplete="off" >
      <label for="bin">Enter value of each resistance in parallel separated by a space in ohms</label>
        <input
          className="calciipinfix"
          onChange={handleChange1}
          type="text"
          placeholder=""
          value={name1}
          id="bin"
          required
        />
      
      
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About Run-Rate</h4>
      <p3>In cricket, the run rate, or runs per over, is the average number of runs a batting side scores per over. It includes all runs made by the batting side in the innings to that point of the game, both the runs scored by the batsmen and extras conceded by the bowling team.</p3>
      <br /><br /><h4>Example</h4>
      <p3>In a Match, if a team scores 312 runs of 50 overs including all extras given by the bowling team, then the run rate is calculated by dividing the total no. of runs divided by total no. overs faced. In this case it is 312/50 which is 6.24. It means that the team has scored 6.21 runs for every over on an average !</p3>
      <br /><br /><h4>You Know What ?</h4>
      <p3>New Zealand has the highest strike rate of 15.83. They achieved this on Dec 31 2007 during a match vs Bangladesh who were bowled out for 93 runs. New Zealand who had to chase 93 runs in 50 overs did it so quick that they chased it in just 6 overs for no loss!</p3>

    </div>
    </div>
  );
}

export default Add;
