import React, { useState } from "react";
import Insidenav from "../insidenav";
var convert = require('convert-units')


function Add() {
  
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
 

 
  const { Convert } = require ('any-to-any');

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
    
  }function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
    
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
 
  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setName4("");
    setName5("");
   
    
  }
  
 
  function handleClick(event) {
    
    if(name3 != ""){var Input = name3; var From = 'm/h';}
    if(name4 != ""){var Input = name4; var From = 'knot';}
    if(name5 != ""){var Input = name5; var From = 'ft/s';}
    
    if(name1 != ""){var Input = name1; var From = 'm/s';}
    if(name2 != ""){var Input = name2; var From = 'km/h';}
    setName1(convert(Input).from(From).to('m/s'));
    setName2(convert(Input).from(From).to('km/h'));
    setName3(convert(Input).from(From).to('m/h'));
    setName4(convert(Input).from(From).to('knot'));
    setName5(convert(Input).from(From).to('ft/s'));
    
   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />

    <div className="container-fluid calci">

      <h1 className="calci">Enter in Any one of the following and click find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">metres per second ( mps ) </label>
      <input className="calciipsmall"  type="text" id="bin" onChange={handleChange1} value={name1} />
      <label for="oct">Kilometres per hour ( kmph ) </label>
      <input className="calciipsmall"  type="text" id="oct" onChange={handleChange2} value={name2} />
      <label for="dec">metres per hour ( mph ) </label>
      <input className="calciipsmall" type="text" id="dec" onChange={handleChange3} value={name3} />
      <label for="dec">Knots </label>
      <input className="calciipsmall"  type="text" id="dec" onChange={handleChange4} value={name4} />
      <label for="dec">feet per second ( ftps ) </label>
      <input className="calciipsmall"  type="text" id="dec" onChange={handleChange5} value={name5} />
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About the number systems</h4>
      <p3>Binary numbers are important because using them instead of the decimal system simplifies the design of computers and related technologies.<br /><br />The main advantage of using Octal numbers is that it uses less digits than decimal and Hexadecimal number system. So, it has fewer computations and less computational errors. It uses only 3 bits to represent any digit in binary and easy to convert from octal to binary and vice-versa.<br /><br />Hexadecimal can be used to write large binary numbers in just a few digits. It makes life easier as it allows grouping of binary numbers which makes it easier to read, write and understand. It is more human-friendly, as humans are used to grouping together numbers and things for easier understanding. </p3>
      
    </div>
    </div>
  );
}

export default Add;
