import React, { useState } from "react";
import Insidenav from "../insidenav";

function Add() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");


  const [headingText1, setHeading1] = useState("");
  const [headingText2, setHeading2] = useState("");

  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setHeading1("");
    setHeading2("");


  }
  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
  }

  function handleClick(event) {

    setHeading1((name1*name3).toFixed(4) +" j ");
    setHeading2(-1*(1/name1*name2).toFixed(4) +" j ");

    event.preventDefault();
  }

  return (
    <div>
             <Insidenav />

    <div className="container-fluid calci">
      <h1 className="calci">X<sub>L</sub> = {headingText1}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;X<sub>c</sub> = {headingText2}&nbsp;</h1>
      <form onSubmit={handleClick}>
      <div className="inner">
        <label className="para1" for="x1"> &nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange1}
          type="number"
          placeholder=""
          value={name1}
          name="x1"
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">C&nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange2}
          type="number"
          placeholder=""
          value={name2}
          
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">L&nbsp;&nbsp;&nbsp;</label>
        <input
          className="calciipunknown para2"
          onChange={handleChange3}
          type="number"
          placeholder=""
          value={name3}
          
        />
        </div>
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      </div>
      <div className="container mt-3 mb-3">
      <h4>About Xl and Xc</h4>
      <p3>Xl is the resistance in ohms of the inductor and Xc is the resistance in ohms of the capacitor when converted from capacitance and farad</p3>
      <br /><br /><h4>How to Use this ?</h4>
      <p3>The omega field is compulsory to fill and you can type any of the inductor value or capacitance value to find the answers</p3>
      <br /><br /><h4>Why should We convert ?</h4>
      <p3>This is generally used in AC circuits when we need to convert inductance and capacitance to ohms finally for better electric circuit calculations </p3>

    </div>
    </div>
  );
}

export default Add;
