import React, { useState } from "react";
import Insidenav from "../insidenav";
var convert = require('convert-units')


function Add() {
  
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");
  const [name4, setName4] = useState("");
  const [name5, setName5] = useState("");
  const [name6, setName6] = useState("");
  const [name7, setName7] = useState("");
  const [name8, setName8] = useState("");
  const [name9, setName9] = useState("");
  const [name10, setName10] = useState("");

  const { Convert } = require ('any-to-any');

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
    
  }function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
    
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
    
  }
  function handleChange4(event) {
    console.log(event.target.value);
    setName4(event.target.value);
  }
  function handleChange5(event) {
    console.log(event.target.value);
    setName5(event.target.value);
  
  }
  function handleChange6(event) {
    console.log(event.target.value);
    setName6(event.target.value);
  
  }
  function handleChange7(event) {
    console.log(event.target.value);
    setName7(event.target.value);
  
  }function handleChange8(event) {
    console.log(event.target.value);
    setName8(event.target.value);
  
  }function handleChange9(event) {
    console.log(event.target.value);
    setName9(event.target.value);
  
  }function handleChange10(event) {
    console.log(event.target.value);
    setName10(event.target.value);
  
  }
  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setName4("");
    setName5("");
    setName6("");
    setName7("");
    setName8("");
    setName9("");
    setName10("");

    
  }
  
 
  function handleClick(event) {
    if(name1 != ""){var Input = name1; var From = 'mm3';}
    if(name2 != ""){var Input = name2; var From = 'cm3';}
    if(name3 != ""){var Input = name3; var From = 'ml';}
    if(name4 != ""){var Input = name4; var From = 'l';}
    if(name5 != ""){var Input = name5; var From = 'kl';}
    if(name6 != ""){var Input = name6; var From = 'm3';}
    if(name7 != ""){var Input = name7; var From = 'tsp';}
    if(name8 != ""){var Input = name8; var From = 'in3';}
    if(name9 != ""){var Input = name9; var From = 'gal';}
    if(name10 != ""){var Input = name10; var From = 'ft3';}

    setName1(convert(Input).from(From).to('mm3'));
    setName2(convert(Input).from(From).to('cm3'));
    setName3(convert(Input).from(From).to('ml'));
    setName4(convert(Input).from(From).to('l'));
    setName5(convert(Input).from(From).to('kl'));
    setName6(convert(Input).from(From).to('m3'));
    setName7(convert(Input).from(From).to('tsp'));
    setName8(convert(Input).from(From).to('in3'));
    setName9(convert(Input).from(From).to('gal'));
    setName10(convert(Input).from(From).to('ft3'));


   
     event.preventDefault();
    
  }

  return (
    <div>
      <Insidenav />

    <div className="container-fluid calci">

      <h1 className="calci">Enter in Any one of the following and click find</h1>

      <form onSubmit={handleClick} autoComplete="off">
      <label for="bin">cubic milli meters ( mm<sup>3</sup> ) </label>
      <input className="calciipsmallmany" placeholder="Enter b" type="text" id="bin" onChange={handleChange1} value={name1} />
      <label for="oct">cubic centi meters ( cm<sup>3</sup> ) </label>
      <input className="calciipsmallmany" placeholder="Enter Kb" type="text" id="oct" onChange={handleChange2} value={name2} />
      <label for="dec">milli litres ( ml ) </label>
      <input className="calciipsmallmany" placeholder="Enter Mb" type="text" id="dec" onChange={handleChange3} value={name3} />
      <label for="dec">Litres ( l ) </label>
      <input className="calciipsmallmany" placeholder="Enter Gb" type="text" id="dec" onChange={handleChange4} value={name4} />
      <label for="dec">Kilo litres ( kl ) </label>
      <input className="calciipsmallmany" placeholder="Enter Tb" type="text" id="dec" onChange={handleChange5} value={name5} />
      <label for="dec">cubic meters ( m<sup>3</sup> ) </label>
      <input className="calciipsmallmany" placeholder="Enter B" type="text" id="dec" onChange={handleChange6} value={name6} />
      <label for="dec"> tea spoons ( tsp ) </label>
      <input className="calciipsmallmany" placeholder="Enter KB" type="text" id="dec" onChange={handleChange7} value={name7} />
      <label for="dec">cubic inches ( in<sup>3</sup> ) </label>
      <input className="calciipsmallmany" placeholder="Enter MB" type="text" id="dec" onChange={handleChange8} value={name8} />
      <label for="dec">Galoons ( gal ) </label>
      <input className="calciipsmallmany" placeholder="Enter GB" type="text" id="dec" onChange={handleChange9} value={name9} />
      <label for="dec">Cubic feet ( ft<sup>3</sup> ) </label>
      <input className="calciipsmallmany" placeholder="Enter TB" type="text" id="dec" onChange={handleChange10} value={name10} />
      
        
        <button className="calcibutton" type="submit">Find Rest All</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
      <h4>About the number systems</h4>
      <p3>Binary numbers are important because using them instead of the decimal system simplifies the design of computers and related technologies.<br /><br />The main advantage of using Octal numbers is that it uses less digits than decimal and Hexadecimal number system. So, it has fewer computations and less computational errors. It uses only 3 bits to represent any digit in binary and easy to convert from octal to binary and vice-versa.<br /><br />Hexadecimal can be used to write large binary numbers in just a few digits. It makes life easier as it allows grouping of binary numbers which makes it easier to read, write and understand. It is more human-friendly, as humans are used to grouping together numbers and things for easier understanding. </p3>
      
    </div>
    </div>
  );
}

export default Add;
