import React, { useState } from "react";
import Insidenav from "../insidenav";

function Add() {
  const [name1, setName1] = useState("");
  const [name2, setName2] = useState("");
  const [name3, setName3] = useState("");


  const [headingText1, setHeading1] = useState("");
  const [headingText2, setHeading2] = useState("");

  function handlereset() {
    setName1("");
    setName2("");
    setName3("");
    setHeading1("");
    setHeading2("");


  }
  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handleChange2(event) {
    console.log(event.target.value);
    setName2(event.target.value);
  }
  function handleChange3(event) {
    console.log(event.target.value);
    setName3(event.target.value);
  }

  function handleClick(event) {
    var mag =  Math.sqrt(name1*name1 + name2*name2);
    var val = name2/name1;


    setHeading1(mag.toFixed(4));
    setHeading2((Math.atan(val)*57.3248407643).toFixed(4) + "degrees");

    event.preventDefault();
  }

  return (
    <div>
             <Insidenav />

    <div className="container-fluid calci">
      <h1 className="calci">Magnitude = {headingText1}&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Phase = {headingText2}</h1>
      <form onSubmit={handleClick}>
      <div className="inner">
        <label className="para1" for="x1"> &nbsp;&nbsp;&nbsp; +&nbsp;&nbsp;&nbsp; </label>
        <input
          className="calciipunknown para2"
          onChange={handleChange1}
          type="number"
          placeholder=""
          value={name1}
          name="x1"
          required
        />
        </div>
        <div className="inner">
        <label className="para1" for="x1">&nbsp;&nbsp;j</label>
        <input
          className="calciipunknown para2"
          onChange={handleChange2}
          type="number"
          placeholder=""
          value={name2}
          required
        />
        </div>
        
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
      </div>
      <div className="container mt-3 mb-3">
      <h4>About Magnitude and Phase</h4>
      <p3>Magnitude is the square root of sum of squares of the values x and y. Phase is the tan inverse of the value y divided by x.</p3>
      <br /><br /><h4>How to Use this ?</h4>
      <p3>If you need to find the magnitude and phase for 3+4j , then enter 3 in the first box and 4 in the next box which will give you the answers once you press the find button.</p3>
      <br /><br /><h4>Why should We convert ?</h4>
      <p3>It is useful in converting it to a Polar format and is basically used in the Electronics domain </p3>

    </div>
    </div>
  );
}

export default Add;
