import React, { useState } from "react";
import Insidenav from "../insidenav";

const infixToPostfix = require('infix-to-postfix');

function Add() {
  const [name1, setName1] = useState("");
  

  const [headingText, setHeading] = useState("");

  function handleChange1(event) {
    console.log(event.target.value);
    setName1(event.target.value);
  }
  function handlereset() {
    setName1("");
    setHeading("");
  }

  function handleClick(event) {
    
    setHeading(infixToPostfix(name1));
    event.preventDefault();
  }

  return (
    <div>
             <Insidenav />

    <div className="container-fluid calci">
      <h1 className="calci">Your Postfix Expression = {headingText}</h1>
      <form onSubmit={handleClick}>
        
        <input
          className="calciipinfix"
          onChange={handleChange1}
          type="text"
          placeholder="Enter Your INFIX expression here"
          value={name1}
        />
        
        <button className="calcibutton" type="submit">Find</button>
        <button onClick={handlereset} className="calcibutton" type="reset">Reset</button>

      </form>
    </div>
    <div className="container mt-3 mb-3">
    <h4>About Postfix Expressions</h4>
    <p3>An expression is called the postfix expression if the operator appears in the expression after the operands.</p3>
    <br /><br /><h4>Example</h4>
    <p3>12+ is the postfix expression for the infix expression 1+2</p3>
    <br /><br /><h4>Why should we convert ?</h4>
    <p3>Infix notation is easy to read for humans, whereas pre-/postfix notation is easier to parse for a machine. The big advantage in pre-/postfix notation is that there never arise any questions like operator precedence.<br />

For example, consider the infix expression 1 # 2 $ 3. Now, we don't know what those operators mean, so there are two possible corresponding postfix expressions: 1 2 # 3 $ and 1 2 3 $ #. Without knowing the rules governing the use of these operators, the infix expression is essentially worthless.<br />

Or, to put it in more general terms: it is possible to restore the original (parse) tree from a pre-/postfix expression without any additional knowledge, but the same isn't true for infix expressions.</p3>

  </div>
  </div>
  );
}

export default Add;
